import React, { useContext, useEffect, useRef, useState } from 'react';

import Nouislider from 'nouislider-react';

import { sendAlert } from '../../../App';

import dummyVideo from '../../../Common/Videos/DummyVideo.mp4';
import 'nouislider/distribute/nouislider.css';

let ffmpeg;

const GetVideoInput = () => {
  const setAlert = useContext(sendAlert);
  const videoRef = useRef();

  let initialSliderValue = 0;

  const [endTime, setEndTime] = useState(0);
  const [videoSrc, setVideoSrc] = useState('');
  const [startTime, setStartTime] = useState(0);
  const [videoDuration, setVideoDuration] = useState(0);
  const [videoFileValue, setVideoFileValue] = useState('');
  const [videoTrimmedUrl, setVideoTrimmedUrl] = useState('');
  const [isScriptLoaded, setIsScriptLoaded] = useState(false);
  const [isVideoPlaying, setIsVideoPlaying] = useState(false);
  const [hasUploadedVideo, setHasUploadedVideo] = useState(false);
  const [currentSliderValue, setCurrentSliderValue] = useState(0);

  const loadScript = async (src) => {
    return new Promise((resolve, reject) => {
      const scripts = document.createElement('script');
      scripts.src = src;
      scripts.async = true;
      scripts.defer = true;
      scripts.addEventListener('load', resolve);
      scripts.addEventListener('error', reject);
      document.head.appendChild(scripts);
    });
  };

  useEffect(() => {
    const loadFfmpeg = async () => {
      try {
        await loadScript('https://cdn.jsdelivr.net/npm/@ffmpeg/ffmpeg@0.11.2/dist/ffmpeg.min.js');
        if (typeof window !== 'undefined') {
          if (!window.FFmpeg) {
            throw new Error('FFmpeg not found on window.FFmpeg');
          }
          ffmpeg = window.FFmpeg.createFFmpeg({ log: true });
          await ffmpeg.load();
          setIsScriptLoaded(true);
        }
      } catch (error) {
        console.error('Failed to load script:', error);
      }
    };
    loadFfmpeg();
  }, []);

  useEffect(() => {
    if (videoRef.current) {
      const currentVideo = videoRef.current;
      currentVideo.onloadedmetadata = () => {
        setVideoDuration(currentVideo.duration);
        setEndTime(currentVideo.duration);
      };
    }
  }, [videoSrc]);

  const updateOnSliderChange = (values, handle) => {
    setVideoTrimmedUrl('');
    let readValue;
    if (handle) {
      readValue = values[handle] | 0;
      if (endTime !== readValue) {
        setEndTime(readValue);
      }
    } else {
      readValue = values[handle] | 0;
      if (initialSliderValue !== readValue) {
        initialSliderValue = readValue;
        setCurrentSliderValue(readValue);
        if (videoRef && videoRef.current) {
          videoRef.current.currentTime = readValue;
          setStartTime(readValue);
        }
      }
    }
  };

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const blobURL = URL.createObjectURL(file);

    setVideoFileValue(file);
    setVideoSrc(blobURL);
    setHasUploadedVideo(true);
  };

  const convertToHHMMSS = (val) => {
    const secNum = parseInt(val, 10);
    let hours = Math.floor(secNum / 3600);
    let minutes = Math.floor((secNum - hours * 3600) / 60);
    let seconds = secNum - hours * 3600 - minutes * 60;

    if (hours < 10) {
      hours = '0' + hours;
    } else if (minutes < 10) {
      minutes = '0' + minutes;
    } else if (seconds < 10) {
      seconds = '0' + seconds;
    }

    let time;
    if (hours === '00') {
      time = minutes + ':' + seconds;
    } else {
      time = hours + ':' + minutes + ':' + seconds;
    }
    return time;
  };

  const handleUpload = () => {
    setAlert('Video uploaded successfully', 'success');
    setHasUploadedVideo(false);
    if (videoRef && videoRef.current) {
      videoRef.current.load();
    }
  };

  const handlePlay = () => {
    if (videoRef && videoRef.current) {
      if (isVideoPlaying) {
        videoRef.current.pause();
      } else {
        videoRef.current.play();
      }
      setIsVideoPlaying(!isVideoPlaying);
    }
  };

  const handlePauseVideo = (e) => {
    const currentTime = Math.floor(e.currentTarget.currentTime);
    if (currentTime === endTime) {
      e.currentTarget.pause();
      setIsVideoPlaying(false);
    }
  };

  const handleReset = () => {
    setAlert('Now you can upload a new video', 'info');
    setHasUploadedVideo(false);
    setVideoSrc('');
  };

  const handleTrim = async () => {
    if (isScriptLoaded && ffmpeg) {
      try {
        setAlert('Trimming video, please wait...', 'info');
        await ffmpeg.run('-i', videoFileValue, '-ss', startTime, '-to', endTime, '-c', 'copy', 'output.mp4');
        const trimmedUrl = URL.createObjectURL(new Blob([ffmpeg.FS('readFile', 'output.mp4')], { type: 'video/mp4' }));
        setVideoTrimmedUrl(trimmedUrl);
        setAlert('Video trimmed successfully', 'success');
      } catch (error) {
        console.error('Failed to trim video:', error);
        setAlert('Failed to trim video', 'error');
      }
    }
  };
  

  return (
    <div className="card px-2 py-3 border-light bg-dark text-light">
      {videoSrc.length || hasUploadedVideo ? (
        <>
          <video
            className={videoTrimmedUrl ? '' : 'video-player'}
            src={videoTrimmedUrl || videoSrc}
            ref={videoRef}
            onTimeUpdate={handlePauseVideo}
            loop
          >
            <source src={videoTrimmedUrl || videoSrc} type={videoFileValue.type} />
          </video>

          <Nouislider
            className="radio mt-3 px-3"
            behaviour="tap-drag"
            step={1}
            range={{ min: 0, max: videoDuration || 2 }}
            start={[0, videoDuration || 2]}
            connect
            onUpdate={(values, handle) => {
              setCurrentSliderValue(values[handle] | 0);
              updateOnSliderChange(values, handle);
            }}
          />

          <div className="d-flex flex-column my-3">
            <span>Start duration: {convertToHHMMSS(startTime)}</span>
            <span>End duration: {convertToHHMMSS(endTime)}</span>
          </div>

          <div className="d-grid ">
            <button
              id="playPauseButton"
              className={`btn ${isVideoPlaying ? 'btn-secondary' : 'btn-success fw-bold '}`}
              onClick={handlePlay}
            >
              {isVideoPlaying ? 'Pause' : 'Play'}
            </button>

            <div className="btn-group my-2">
              <button className="btn btn-success text-light fw-bold btn-outline-success" onClick={handleTrim}>
                Trim {convertToHHMMSS(currentSliderValue)}
              </button>

              <button className="btn btn-danger btn-outline-secondary text-light border-0" onClick={handleUpload}>
                Upload video
              </button>

              <button className="btn btn-success btn-outline-danger text-light border-0" onClick={handleReset}>
                Reset
              </button>
            </div>
          </div>
        </>
      ) : (
        <div>
          <input className="form-control mb-2" type="file" onChange={handleFileUpload} />
          <video className="img-fluid" src={dummyVideo} ref={videoRef} onTimeUpdate={handlePauseVideo} loop autoPlay />
        </div>
      )}
    </div>
  );
};

export default GetVideoInput;
