import React, { createContext, useState } from 'react';

import Alert from './Common/Alert/Alert';
import Loader from './Assets/Loader/Loader.gif';
import MainPage from './Pages/MainPage';

export const sendAlert = createContext(null);
export const getLoader = createContext(null);

function App() {

  const [alert, setAlert] = useState(false);

  const showAlert = (message, type) => {

    setAlert({
      msg: message,
      type: type
    });

    setTimeout(() => {
      setAlert(false);
    }, 4000);

  }

  return (
    <div className='container-fluid p-0'>
      <sendAlert.Provider value={showAlert}>
        <getLoader.Provider value={Loader} >
          <Alert alert={alert} />
          <MainPage />
        </getLoader.Provider>
      </sendAlert.Provider>
    </div>

  );
}

export default App;
