import React, { useRef } from 'react';

import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import { sliderCardData } from '../../services/Services';

export default function VideoCrousal() {

    const videoRef = useRef(null);

    var settings = {
        dots: true,
        infinite: true,
        speed: 1000,
        slidesToShow: 3,
        slidesToScroll: 1,
        useCSS: true,

    };

    const responsive = {
        desktop: {
            breakpoint: { max: 3000, min: 1024 },
            items: 3,
            slidesToSlide: 3 // optional, default to 1.
        },
        tablet: {
            breakpoint: { max: 1024, min: 464 },
            items: 2,
            slidesToSlide: 2 // optional, default to 1.
        },
        mobile: {
            breakpoint: { max: 464, min: 0 },
            items: 1,
            slidesToSlide: 1 // optional, default to 1.
        }
    };

    const handlePlayvideo = () => {
        if (videoRef.current) {
            videoRef.current.play();
        }

    }

    return (

        <div className='py-5 ' >
            {/* autoPlay={true} */}

            <Carousel {...settings} responsive={responsive} containerClass="carousel-container"
                // removeArrowOnDeviceType={["tablet", "mobile"]}
                showDots={true}
                infinite={true}
                focusOnSelect={true}
                dotListClass="custom-dot-list-style"
                itemClass="carousel-item-padding-40-px" >
                {
                    sliderCardData.map((data, Index) => (
                        <div className='' key={Index}>
                            <div className='card mx-3 p-0'>
                                <div className="card-img">
                                    <video ref={videoRef} className='img-fluid ' controls src={data.video}></video>
                                </div>
                                <div className="card-body">
                                    <div className="card-text">{data.cardtext}</div>
                                </div>

                                <button className='btn text-danger fs-5 bg-dark bg-opacity-25 m-2' onClick={handlePlayvideo}>PLAY NOW</button>
                            </div>


                        </div>
                    ))
                }

            </Carousel>
        </div>
    )
}
