import React, { useRef, useState, useEffect, useContext } from 'react';

import WaveSurfer from 'wavesurfer.js';
import Nouislider from 'nouislider-react';

import 'nouislider/distribute/nouislider.css';

import { sendAlert } from '../../../App';

const GetVoice = () => {
  const setAlert = useContext(sendAlert);

  const audioRef = useRef(null);
  const wavesurferRef = useRef(null);
  const mediaRecorderRef = useRef(null);

  const [isRecording, setIsRecording] = useState(false);
  const [recordingTime, setRecordingTime] = useState(0);
  const [cropStart, setCropStart] = useState(0);
  const [cropEnd, setCropEnd] = useState(1);

  useEffect(() => {
    wavesurferRef.current = WaveSurfer.create({
      container: '#waveform',
      waveColor: 'violet',
      progressColor: 'purple',
      cursorWidth: 1,
      height: 100,
    });

    return () => {
      if (wavesurferRef.current) {
        wavesurferRef.current.destroy();
      }
    };
  }, []);

  useEffect(() => {
    let timer;
    if (isRecording) {
      timer = setInterval(() => {
        setRecordingTime((prevTime) => prevTime + 1);
      }, 1000);
    }
    return () => {
      clearInterval(timer);
    };
  }, [isRecording]);

  const handleStartRecording = async () => {
    setAlert("Recording Start", "success");

    if (isRecording) {
      handleStopRecording();
      return;
    }

    try {
      const stream = await navigator.mediaDevices.getUserMedia({ audio: true });

      const mediaRecorder = new MediaRecorder(stream);
      mediaRecorder.recordedData = [];
      mediaRecorderRef.current = mediaRecorder;

      mediaRecorder.ondataavailable = (event) => {
        if (event.data.size > 0) {
          setAlert("Recording Stop", "success");
          mediaRecorder.recordedData.push(event.data);
        }
      };

      mediaRecorder.onstop = () => {
        setIsRecording(false);

        const blob = new Blob(mediaRecorderRef.current.recordedData, { type: 'audio/wav' });
        const audioUrl = URL.createObjectURL(blob);

        wavesurferRef.current.load(audioUrl);

        if (audioRef.current) {
          audioRef.current.src = audioUrl;
        }

        setCropStart(0);
        setCropEnd(wavesurferRef.current.getDuration());
        setRecordingTime(0); 

        mediaRecorderRef.current.recordedData = [];
      };

      mediaRecorder.start();
      setIsRecording(true);

    } catch (error) {
      console.error('Error accessing microphone:', error);
    }
  };

  const handleStopRecording = () => {
    if (mediaRecorderRef.current && isRecording) {
      mediaRecorderRef.current.stop();
    }
  };

  const handleCropChange = (values) => {
    const [start, end] = values.map(parseFloat);
    setCropStart(start);
    setCropEnd(end);
    setAlert("Crop Time", "success");
  };

  const handleCropAudio = () => {
    if (mediaRecorderRef.current) {

      const selectedBlob = new Blob(mediaRecorderRef.current.recordedData.slice(cropStart, cropEnd), {
        type: 'audio/wav',
      });

      const selectedAudioUrl = URL.createObjectURL(selectedBlob);

      if (audioRef.current) {
        audioRef.current.src = selectedAudioUrl;
      }

      setRecordingTime(0);

      wavesurferRef.current.stop();

      wavesurferRef.current.load(selectedAudioUrl);

      setAlert("Audio Cropped", "success");
    }
  };

  return (
    <div className="card bg-dark border-light">
      <div className='d-flex p-2 flex-column justify-content-evenly'>
        <div className='mb-2'>
          <button
            className={`btn py-2 btn-sm ${isRecording ? 'btn-danger' : 'btn-success'} border-0 col-12`}
            style={{ cursor: 'pointer' }}
            onClick={handleStartRecording}
            disabled={isRecording} 
          >
            {isRecording ? `Stop Recording ${recordingTime} seconds` : 'Start Recording'}
          </button>
        </div>

        {!isRecording && (
          <>
            <div className='mb-1'>
              <label className='d-block mb-2 text-light' htmlFor="cropTime">
                Crop Time: {cropStart} - {cropEnd} seconds
              </label>
              <Nouislider
                range={{ min: 0, max: wavesurferRef.current?.getDuration() || 10 }}
                start={[cropStart, cropEnd]}
                step={0.1}
                connect
                onChange={handleCropChange}
              />
            </div>

            <button
              className='btn py-2 btn-sm btn-success border-0'
              style={{ cursor: 'pointer' }}
              onClick={handleCropAudio}
              disabled={!audioRef.current} 
            >
              Crop Audio
            </button>

            <div className='d-none' id="waveform"></div>

            <audio
              className='form-control'
              controls
              style={{ marginTop: '10px' }}
              ref={(audio) => (audioRef.current = audio)}
            ></audio>
          </>
        )}
      </div>
    </div>
  );
};

export default GetVoice;
