import React from 'react';

import Logo from "../Common/Logo/Logo.png";

import InputsPage from './Getinputs/InputsPage';
import HelloPage from './HelloPage';

import NeuralNetwork from './neuralnetwork/NeuralNetwork';

import { Routes, Route } from 'react-router-dom';

export default function Welcome() {

    const setAlignment = {
        height: '100vh',
    }

    return (

        <div className='' style={setAlignment} >

            <Routes>

                <Route path='/' element={<HelloPage Logo={Logo} />} />
                <Route path='inputspage' element={<InputsPage Logo={Logo} />} />
                <Route path='neuralnetwrok' element={<NeuralNetwork Logo={Logo} />} />

            </Routes>

        </div>

    )
}

